








import 'sanitize.css'
import Setting from './components/Setting'
import Game from './components/Game'

export default
  name: 'app'
  data: ->
    mode: 'setting'
    color: null
    level: null
    guide: null
  methods:
    start: (color, level, guide) ->
      @color = color
      @level = level
      @guide = guide
      @mode = 'game'
    back: ->
      @mode = 'setting'
  components: {
    Setting
    Game
  }
